@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-img {
	width: 75px;
	height: auto;
}

.header {
	max-width: 250px;
}

.header-inner {
  background: linear-gradient(to left, rgb(217, 244, 255), rgb(140, 222, 255));
}
.headerBar {
    margin: 0 0 0 0;
    font-size: 100%;
    position: fixed;
    width: 100%;
    height:50px;
    overflow: hidden;
}

.burger {
	display: flex;
	float: right;
	padding-right: 20px;
	padding-top: 5px;
	height: 40px;
	-webkit-filter: invert(100%) sepia(96%) saturate(15%) hue-rotate(212deg) brightness(104%) contrast(104%);
	        filter: invert(100%) sepia(96%) saturate(15%) hue-rotate(212deg) brightness(104%) contrast(104%);
}

.cancel {
	display: flex;
	float: right;
	padding-right: 25px;
	padding-top: 10px;
	height: 30px;
	-webkit-filter: invert(100%) sepia(96%) saturate(15%) hue-rotate(212deg) brightness(104%) contrast(104%);
	        filter: invert(100%) sepia(96%) saturate(15%) hue-rotate(212deg) brightness(104%) contrast(104%);
}


.burger:hover {
	cursor: pointer;
}

.logo {
	display: flex;
	float: left;
	padding-left: 20px;
	padding-top: 7px;
	height: 35px;
	-webkit-filter: invert(100%) sepia(96%) saturate(15%) hue-rotate(212deg) brightness(104%) contrast(104%);
	        filter: invert(100%) sepia(96%) saturate(15%) hue-rotate(212deg) brightness(104%) contrast(104%);

}

.title {
	display: flex;
	float: left;
	margin-top: 12px;
	padding-left: 10px;
	color: white;

}
li {
	list-style:none;
}

.nav {
	float: left;
	text-align: left;
}


.input-bar {
  background-color:#def;
  background-image: radial-gradient(closest-side, transparent 98%, rgba(0,0,0,.3) 99%),
  radial-gradient(closest-side, transparent 98%, rgba(0,0,0,.3) 99%);
  background-size:80px 80px;
  background-position:0 0, 40px 40px;
  }

.input {
  top:20px;
}

.descriptors-container {
  padding-top: 20px;

}

.input-container {
  display: flex;
  flex-flow: wrap;
}

.input-container hr {
  width: 100%;
  clear: both;
  visibility: hidden;

}

.descriptor {
  display: inline-block;
  margin-left: 10px;
  margin-right: 10px;
}

.cross {
  font-size: 1.7em;
}

.descriptor-container {
  position: relative;
}

.descriptor-inner {
  position: absolute;
  top:0;
  right:0;
}

.background-colour {
  background-color: white;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.slider {
  width: 500px;
}

.mood {
  background: linear-gradient(to right, rgb(194, 255, 212), rgb(5, 255, 147));;
}

.energy {
  background: linear-gradient(to right, rgb(194, 232, 255), rgb(5, 205, 255));
}

.productivity {
  background: linear-gradient(to right, rgb(251, 255, 194), rgb(201, 255, 5));
}


.App {
  text-align: center;
}

html,body {
  font-family:Source Sans Pro,Helvetica,Arial,sans-serif;
  font-size:14px;
  line-height:1.3;
  height:100%;
  background: linear-gradient(to right, rgb(217, 244, 255), rgb(140, 222, 255));
  position: fixed;
  top:0;
  right:0;
  left: 0;
  bottom: 0;

  }

.container {
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  padding-right: 20px;
}

.container-pad {
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  padding-right: 20px;
}

.particles {
  position: fixed;
  top:0;
  right:0;
  left: 0;
  bottom: 0;
  z-index: -1;
}

.center {
  display: flex;
  justify-content: center;
}

.main {
  margin-left: 250px; /* Same as the width of the sidebar */
}

.side {
  height: 100%; 
  position: fixed; 
  z-index: 1; 
  top: 0; 
  left: 0;
  overflow-x: hidden; /* Disable horizontal scroll */
}

.nb:focus {
  outline: none;
}

.statscard {
  display: inline-block;
}


@media screen and (max-width: 599px) {
     /* start of phone styles */
     /* It's possible to hide the image if the screen becomes too small */

     .main {
      margin-left: 0px; /* Same as the width of the sidebar */
      width: auto;
      margin-top: 50px;
}

 .nav-drawer {
  margin-top: 0px;
  height: 100%;
  position: fixed;
  transition: 1s;
  left: -200px;
  width: 200px;
  z-index: 99;
  margin-top: 50px;

 }

 .nav-drawer-active {
  margin-top: 0px;
  height: 100%;
  position: fixed;
  transition: 1s;
  left: 0;
  width: 100%;
  z-index: 99;
  background-color: white;
  margin-top: 50px;
 }
}
